<template>
  <div class="courses-box">
    <div class="banner-box" :style="{'background-image': `url('${publicPath}image/news_1.png')`}">
       {{ info.name }}
    </div>
    <div class="courses-content">
      <template v-for="(item, i) in info.children">
        <h1 :key="i+ 's'">{{ item.name }}</h1>
        <ul :key="i+ 'ss'" class="list-box">
          <li v-for="(unit, j) in item.children" :key="j" class="list-item" @click="goLink(`/coursesUnit/${unit.id}`)">
            <img :src="coursesImg" alt="">
            <h2>{{ unit.name }}</h2>
            <p>{{ unit.title }}</p>
          </li>
        </ul>
      </template>
    </div>
  </div>
</template>
<script>
import { getSubject } from '@/api'
import coursesImg from '@/assets/courses_1.png'
export default {
  props: {
    keyword: {
      type: [String, Number],
      default: () => ''
    }
  },
  data() {
    return {
      coursesImg,
      publicPath: process.env.BASE_URL,
      info: {}
    }
  },
  watch: {
    keyword: {
      handler() {
        this.getData()
      },
      immediate: true
    }
  },
  methods: {
    goLink(path) {
      this.$router.push({ path })
    },
    getData() {
      getSubject({ id: this.keyword }).then(res => {
        this.info = res
      }).catch(() => {})
    }
  }
}
</script>
